html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.block {
  display: flex;
}

.header__inner {
  display: flex;
  align-items: center;
}

.wrap {
  width: 1170px;
  margin: 0 auto;
}

.bg-blue {
  background-color: #00bdca;
}

.header-wrap {
  padding: 66px 0;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end;
}

.logo {
  display: flex;
}

.logo__text {
  margin-left: 12px;
}

.logo__text p {
  color: #fff;
  color-font-weight: 600;
  font-size: 24px;
}

.logo__text p span {
  color: #56ecf6;
}

.logo__text p:nth-child(2) {
  font-size: 13px;
  font-weight: 400;
  line-height: 3px;
}

.logo__text p {
  font-weight: 600;
}

.header-right {
  display: flex;
  align-items: center;
}

.btn {
  background-color: #ffffff;
  color: #1d354e;
  padding: 15px 25px;
  font-size: 16px;
  border-radius: 16px;
  font-weight: 600;
}

.lang {
  position: relative;
  color: #1d354e;
  border-radius: 21px;
  background-color: #ffffff;
  font-weight: 600;
  margin-left: 8px;
}

.lang:hover .lang-list {
  opacity: 1;
  visibility: visible;
}

.lang:hover .lang__current {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

.lang-list {
  position: absolute;
  top: 100%;
  left: 0px;
  opacity: 0;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #ffffff;
  border-bottom-right-radius: 16px;
  border-bottom-left-radius: 16px;
}

.lang-list__item {
  color: #1d354e;
  padding: 15px 25px;
  font-size: 16px;
  font-weight: 600;
}

.lang__link {
  text-decoration: none;
  color: #1d354e;
}

.lang__current:hover {
  text-decoration: underline;
}

.lang__link:hover {
  text-decoration: underline;
  cursor: pointer;
}

.menu {
  display: flex;
  align-items: center;
  font-weight: 600;
}

.menu__item a {
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
  margin-right: 30px;
}

p {
  color: red;
}
