.header {
	&__inner {
		display: flex;
		align-items: center; }
 }	// height: 100vh

.wrap {
	width: 1170px;
	margin: 0 auto; }

.bg-blue {
	background-color: #00bdca; }

.header-wrap {
	padding: 66px 0;
	display: flex;
	align-items: center;
	width: 100%;
	justify-content: flex-end; }
.logo {
	display: flex;
	&__text {
		margin-left: 12px;

		p {
			color: $white {
            font-weight: 600; }
			font-size: 24px;
			span {
				color: #56ecf6; } }
		p:nth-child(2) {
			font-size: 13px;
			font-weight: 400;
			line-height: 3px; } } }
.logo__text p {
    font-weight: 600; }

.header-right {
	display: flex;
	align-items: center; }

.btn {
	background-color: #ffffff;
	color: #1d354e;
	padding: 15px 25px;
	font-size: 16px;
	border-radius: 16px;
	font-weight: 600; }

.lang {
	position: relative;
	color: #1d354e;
	border-radius: 21px;
	background-color: #ffffff;
	font-weight: 600;
	margin-left: 8px;

	&:hover {
		.lang-list {
			opacity: 1;
			visibility: visible; } }
	&:hover .lang__current {
				border-bottom-left-radius: 0px;
				border-bottom-right-radius: 0px; } }



.lang-list {
	position: absolute;
	top: 100%;
	left: 0px;
	opacity: 0;
	visibility: hidden;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	background-color: #ffffff;
	border-bottom-right-radius: 16px;
	border-bottom-left-radius: 16px;

	&__item {
		color: #1d354e;
		padding: 15px 25px;
		font-size: 16px;
		font-weight: 600; } }

.lang__link {
	text-decoration: none;
	color: #1d354e; }

.lang__current:hover {
	text-decoration: underline; }

.lang__link:hover {
	text-decoration: underline;
	cursor: pointer; }







